


























































import Vue from "vue";
import eventBus from "@/eventBus";
import { User } from "@/models";
export default Vue.extend({
  name: "Result",
  mounted: function() {
    eventBus.$on("onDrawEvent", this.setResult);
  },
  destroyed: function() {
    eventBus.$off("onDrawEvent");
  },
  methods: {
    setResult(result: Map<number, User[]>) {
      this.result = result;
    },
    getTimeTurn(turnNumber: number) {
      switch (turnNumber) {
        case 0:
          return "10:15";
        case 1:
          return "10:30";
        case 2:
          return "10:45";
        case 3:
          return "11:00";
        default:
          return "N/A";
      }
    },
    copy() {
      this.$copyText(this.getTextToCopy()).then(
        this.onCopySuccess,
        this.onCopyError
      );
    },
    onCopySuccess(e: any) {
      this.$message({
        message: "Copia de los turnos realizada.",
        type: "success",
        showClose: true
      });
    },
    onCopyError(e: any) {
      this.$message({
        message: "Ups, ha habido un error en la copia de los turnos.",
        type: "error",
        showClose: true
      });
    },
    getTextToCopy() {
      const values = Array.from(this.result.values());

      return values
        .map(
          (turn, index) =>
            `Turno ${index + 1}: [${turn
              .map(user =>
                index != 0
                  ? user.name.concat(user.default ? "ðŸ”’" : "")
                  : user.name
                      .concat(" ( @")
                      .concat(user.alias)
                      .concat(" )")
                      .concat(user.default ? "ðŸ”’" : "")
              )
              .join(", ")}]`
        )
        .join("\n");
    }
  },
  data: () => ({
    result: new Map<number, User[]>()
  })
});
